import React from 'react'
import Error404 from '../images/error-404.svg'

const NotFoundPage = () => (
  <div className="main-wrapper animated fadeIn">
    <div className="errorImg">
      <img src={Error404} alt="Phanor Coll" />
      <p>ups!! you got here by mistake</p>
      <p>use the top nav bar for easy access</p>
    </div>
  </div>
)

export default NotFoundPage



